<template>
    <div>
        <div class="super_container single_post_quote">
            <div>
                <p class="text-center color-white" style="background-color: #f23344; font-size: 24px; color: #fff">ID Information</p>
            </div>
		    <div class="container">
                <p class="text-dark pt-2">You can upload one of the following government issued ID documents: Passport, National ID Card, Driver's License
                </p>
                <div class="container">
                    <div class="row">
                        <div class="col-12 col-sm-6 col-md-8">
                            <h6 class="text-dark pt-2">Requirements for your ID:</h6>
                            <ul class="custom-bullet">
                                <li>Entire ID is visible</li>
                                <li>ID is clear and easy to read</li>
                                <li>Front side (if submitting National ID or Driver's License)</li>
                                <li>Front side (if submitting Passport)</li>
                                <li>Note with words "Coin" + today's date</li>
                                <li>PDF, JPG, PNG (max size 10MB)</li>
                            </ul>
                        </div>
                        <div class="col-6 col-md-4 p-2">
                            <div>
                                <img class="img-responsive col-xs-12" src="http://www.ltoexamreviewer.com/wp-content/uploads/2016/02/non-professional-ltoexamreviewer.jpg">
                            </div>
                        </div>
                    </div>
                    <div class="pt-3">
                        <!-- Plain mode -->
                        <b-form-file v-model="file2" class="mt-3" plain></b-form-file>
                        <div class="mt-3">Selected file: {{file2 && file2.name}}</div>
                    </div>
                    <div>
                    </div>
                    <div class="row pt-5 mt-5">
                        <div class="col-12 col-sm-6 col-md-8">
                            <h6 class="text-dark pt-2">Requirements for your photo + ID:</h6>
                            <ul class="custom-bullet">
                                <li>Face clearly visible, holding ID and note next to face</li>
                                <li>ID clearly visible</li>
                                <li>Note with words "Coin" + today's date</li>
                                <li>PDF, JPG, PNG (max size 10MB)</li>
                            </ul>
                        </div>
                        <div class="col-6 col-md-4 p-2">
                            <div>
                                <img class="img-responsive col-xs-12" src="http://www.ltoexamreviewer.com/wp-content/uploads/2016/02/non-professional-ltoexamreviewer.jpg">
                            </div>
                        </div>
                    </div>
                    <div class="pt-3">
                        <!-- Plain mode -->
                        <b-form-file v-model="file2" class="mt-3" plain></b-form-file>
                        <div class="mt-3">Selected file: {{file2 && file2.name}}</div>
                    </div>
                    <div class="d-flex justify-content-center pt-3">
                        <router-link to="/Verify">
                            <button id="submit" name="submit" class="pdetail-button pd-sell">Cancel</button>
                        </router-link>
                        <router-link to="/Complete">
                            <button id="submit" name="submit" class="pdetail-button pd-buy ml-2">Submit</button>
                        </router-link>
                    </div>
                </div>              
		    </div>
        </div>
    </div>
</template>

<script>
export default {
  data () {
    return {
      file: null,
      file2: null
    }
  }
}
</script>

<style scoped>
.custom-bullet li {
    display: block;
}

.custom-bullet li:before
{
    /*Using a Bootstrap glyphicon as the bullet point*/
    content: "⦿";
    font-size: 10px;
    float: left;
    margin-top: 3px;
    margin-left: 1%;
    padding-right: 2%;
}
#customFile .custom-file-control:lang(en)::after {
  content: "Select a file...";
}

#customFile .custom-file-control:lang(en)::before {
    content: "Browse";
    background-color: #e40e21;
    color: #fff
}

/*when a value is selected, this class removes the content */
.custom-file-control.selected:lang(en)::after {
  content: "" !important;
}

.custom-file {
    overflow: hidden;
    width: 100%;
}
.custom-file-control {
    white-space: nowrap;
    text-overflow: ellipsis;
}
.custom-file-input{
    cursor: pointer;
}
img{
    width: 100%
}
@media only screen and (max-width: 600px)
{
	img{
        width: 240px;
        height: 130px
    }
}
</style>






